import React from 'react'
import "../Contact/Contact.css";
import emailjs from "emailjs-com";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';





export default function Contact() {

    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm('service_l216exp', 'template_apgelpc', e.target, 'user_fpXMiexNSQWcd6sC68DAs')
            .then((result) => {
                console.log(result.text);


            }, (error) => {
                console.log(error.text);
                alert("Error Occured");
                window.location.reload();

            });
            toast("Message Sent Successfully");
            e.target.reset();

    }



    return (
        <div>
            <div className="postemail container col-lg-6">
                <form id="btnsubmit" className="post_form" onSubmit={(e) => { sendEmail(e) }}>
                    <h4 style={{ textAlign: "center" }}>Send Message</h4>
                    <div className="form-group ">
                        <label htmlFor="title">Your Name</label>
                        <input type="text" name="name" className="form-control" required placeholder="ex . Jon Snow" />
                    </div>
                    <div className="form-group">
                        <label>Email ID</label>
                        <input type="text" name="email" className="form-control" required placeholder="example@xyz.com"/>
                    </div>
                    <div className="form-group">
                        <label>Subject</label>
                        <input type="text" name="subject" className="form-control" required />
                    </div>
                    <div className="form-group">
                        <label>Message</label>
                        <textarea name="message" class="form-control" id="exampleFormControlTextarea1" rows="3" required></textarea>
                    </div>

                    <button style={{ marginTop: "10px" }} type="submit" className="btn btn-success">SEND !</button>

                </form>
                <ToastContainer/>
            </div>
        </div>
    )
}
